import React from "react";
import { Link } from "react-router-dom";
import "./index.css";
const Home = props => (
    <div>
        <div className="header">
            <div className="header-button">
                <span>
                    <Link to="/"> Home </Link>
                </span>
            </div>
            <div className="header-button">
                <span>
                    <Link to="/reactJS"> RJS </Link>
                </span>
            </div>
            <div className="header-button">
                <span>
                    <Link to="/reactNative"> RN </Link>
                </span>
            </div>
        </div>

        <span> REACT NATIVE PAGE {props.data}</span>
    </div>
);

export default Home;



