import React, { Component } from "react";
import { connect } from "react-redux";
import * as homeActions from "../actions/Home";
import { bindActionCreators } from "redux";
import RNComponent from "../components/ReactNative";

class ReactNative extends Component {
    render() {
        const { getHomeData, data } = this.props;
        return <RNComponent data={data} getHomeData={getHomeData} />;
    }
}

function mapStateToProps(state) {
    return {
        data: state.Home.data
    };
}

const mapDispatchToProps = dispatch =>
    bindActionCreators(
        {
            ...homeActions
        },
        dispatch
    );

export default connect(mapStateToProps, mapDispatchToProps)(ReactNative);



