import React from "react";
import { Link } from "react-router-dom";
import "./index.css";
import Block from "./Block";
const DATA = [
    { name: "React JS", link: "/reactJS" },
    { name: "React Native", link: "/reactNative" }
];

const Home = props => (
    <div>
        <div className="header">
            <div className="header-button">
                <span>
                    <Link to="/"> Home </Link>
                </span>
            </div>
            <div className="header-button">
                <span>
                    <Link to="/reactJS"> RJS </Link>
                </span>
            </div>
            <div className="header-button">
                <span>
                    <Link to="/reactNative"> RN </Link>
                </span>
            </div>
        </div>
        <hr />
        <span> Introduction, HOME PAGE {props.data}</span>
        <hr />
        <span> Technologies </span>
        <div className="block-container">{DATA.map(item => <Block data={item} />)}</div>
        <hr />
        <span> My react Native Projects</span>
    </div>
);

export default Home;



